export default {
	data() {
		return {
			
		};
	},
	methods:{
		toAbout(){
			uni.navigateTo({
				url:'/pages/about/about'
			})
		},
		loginOut(){
			uni.showModal({
				title: '温馨提示',
				content: '确定要退出当前登录?',
				success: (res)=> {
					if (res.confirm) {
						try {
							uni.removeStorageSync('TOKEN');
							uni.showToast({
								title:'退出成功',
								icon:'none',
								mask:true
							})
							setTimeout(()=>{
								uni.switchTab({
									url:'/pages/mine/mine'
								})
							},1000)
						} catch (e) {
							uni.showToast({
								title:'退出失败',
								icon:'none',
								mask:true
							})
						}
					} else if (res.cancel) {
						uni.showToast({
							title:'取消退出',
							icon:'none'
						})
					}
				}
			});
			
		}
	}
}
